.modal--wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;

    .modal__content {
        padding: 25px;
        background: white;
        border-radius: 16px;
        min-width: 250px;
    }
}